window.esign = window.esign || {};

esign.init = function () {
  $('html').removeClass('no-js').addClass('js');

  Response.create({
    prop: "width",
    prefix: "min-width- r src",
    breakpoints: [767,0],
    lazy: true
  });

  esign.cacheSelectors();
  esign.navigation();
  esign.responsiveVideos();
  esign.matchHeight();
  esign.placeholder();
  esign.scrollSpy();
  esign.modals();
  esign.countryList();
  esign.map();
  esign.starListeners();
  esign.qtip();

  $('#custom-pledge-close').fadeOut();
  $('#how-to-read-close').fadeOut();
  $('#how-to-read-close2').fadeOut();
};

