/***********************************
 * Warming Map
 ***********************************/
esign.warmingMap = function() {
  esign.resetState();
  esign.cache.warmingMode = 2;
  esign.loadWarmingMapData();

  var $temperatureToggle = $('.temperature-toggle');
  var $temperatureToggleUpdated = $('.temperature-toggle-updated');
  var $temperatureToggleSource = $('.temperature-toggle-source');
  var $triggerLow = $('#low');
  var $triggerAvg = $('#avg');
  var $triggerHigh = $('#high');
  var $triggerLowUpdated = $('#low-updated');
  var $triggerHighUpdated = $('#high-updated');
  var $triggerFirst = $('#first-ndc');
  var $triggerUpdated = $('#updated-ndc');
  var $triggerFirstGraph = $('#first-ndc-graph');
  var $triggerUpdatedGraph = $('#updated-ndc-graph');
  var $triggerSource1 = $('#climate-action-tracker');
  var $triggerSource2 = $('#meinshausen');
  // var $triggerSourceCurrent = $('#temperature-toggle-source .active');
  var $fairnessFirst = $('.fairness-statement-first');
  var $fairnessUpdated = $('.fairness-statement-updated');

  // Low
  $triggerLow.click(function (e) {
    e.preventDefault();

    if (esign.cache.warmingMode !== 2) {
      esign.resetState();
      esign.cache.warmingMode = 2;
      $triggerAvg.removeClass('active');
      $triggerHigh.removeClass('active');
      $triggerLow.addClass('active');

      $('#two-graph').removeClass('active');
      $('#one-graph').addClass('active');
      esign.loadWarmingMapData();
    }
  });

  // Avg
  $triggerAvg.click(function (e) {
    e.preventDefault();

    if (esign.cache.warmingMode !== 0) {
      esign.resetState();
      esign.cache.warmingMode = 0;
      $triggerHigh.removeClass('active');
      $triggerLow.removeClass('active');
      $triggerAvg.addClass('active');

      $('#one-graph').removeClass('active');
      $('#two-graph').addClass('active');
      esign.loadWarmingMapData();
    }
  });

  // High
  $triggerHigh.click(function (e) {
    e.preventDefault();

    if (esign.cache.warmingMode !== 1) {
      esign.resetState();
      esign.cache.warmingMode = 1;
      $triggerLow.removeClass('active');
      $triggerAvg.removeClass('active');
      $triggerHigh.addClass('active');

      $('#one-graph').removeClass('active');
      $('#two-graph').addClass('active');
      esign.loadWarmingMapData();
    }
  });

  // Low updated
  $triggerLowUpdated.click(function (e) {
    e.preventDefault();

    if (esign.cache.warmingMode !== 2) {
      esign.resetState();
      esign.cache.warmingMode = 2;
      $triggerHighUpdated.removeClass('active');
      $triggerLowUpdated.addClass('active');

      $('#two-graph').removeClass('active');
      $('#one-graph').addClass('active');
      esign.loadWarmingMapData();
    }
  });

  // High updated
  $triggerHighUpdated.click(function (e) {
    e.preventDefault();

    if (esign.cache.warmingMode !== 1) {
      esign.resetState();
      esign.cache.warmingMode = 1;
      $triggerLowUpdated.removeClass('active');
      $triggerHighUpdated.addClass('active');

      $('#one-graph').removeClass('active');
      $('#two-graph').addClass('active');
      esign.loadWarmingMapData();
    }
  });

  // First NDC
  $triggerFirst.click(function (e) {
    e.preventDefault();

    if (esign.cache.warmingModeUpdated !== false && !$triggerFirst.hasClass('active')) {
      esign.resetState();
      esign.cache.warmingModeUpdated = false;
      esign.cache.warmingMode = 2;
      $temperatureToggle.removeClass('hide');
      $temperatureToggleUpdated.addClass('hide');
      $temperatureToggleSource.addClass('hide');
      $triggerUpdated.removeClass('active');
      $triggerFirst.addClass('active');
      $triggerUpdatedGraph.removeClass('active');
      $triggerFirstGraph.addClass('active');
      $triggerLow.addClass('active');
      $triggerHigh.removeClass('active');
      $triggerAvg.removeClass('active');
      esign.loadWarmingMapData();
      esign.checkListVisibleCountries();
      $fairnessFirst.removeClass('hide');
      $fairnessUpdated.addClass('hide');
    }
  });

  // Updated NDC
  $triggerUpdated.click(function (e) {
    e.preventDefault();
    if (esign.cache.warmingModeUpdated !== true && !$triggerUpdated.hasClass('active')) {
      esign.resetState();
      esign.cache.warmingModeUpdated = true;
      esign.cache.warmingMode = 2;
      esign.cache.warmingSource = 1;
      $temperatureToggle.addClass('hide');
      $temperatureToggleUpdated.removeClass('hide');
      $temperatureToggleSource.removeClass('hide');
      $triggerFirst.removeClass('active');
      $triggerUpdated.addClass('active');
      $triggerFirstGraph.removeClass('active');
      $triggerUpdatedGraph.addClass('active');
      // $triggerSourceCurrent.text($triggerSource2.text());
      $triggerSource2.addClass('active');
      $triggerSource1.removeClass('active');
      $triggerLowUpdated.addClass('active');
      $triggerHighUpdated.removeClass('active');
      esign.loadWarmingMapData();
      esign.checkListVisibleCountries();
      $fairnessFirst.addClass('hide');
      $fairnessUpdated.removeClass('hide');
    }
  });

  // Climate Action Tracker
  $triggerSource1.click(function (e) {
    e.preventDefault();
    if (!$triggerSource1.hasClass('active')) {
      esign.resetState();
      esign.cache.warmingSource = 0;
      // $triggerSourceCurrent.text($triggerSource1.text());
      $triggerSource2.removeClass('active');
      $triggerSource1.addClass('active');
      esign.loadWarmingMapData();
    }
  });

  // Meinshausen
  $triggerSource2.click(function (e) {
    e.preventDefault();
    if (!$triggerSource2.hasClass('active')) {
      esign.resetState();
      esign.cache.warmingSource = 1;
      // $triggerSourceCurrent.text($triggerSource2.text());
      $triggerSource1.removeClass('active');
      $triggerSource2.addClass('active');
      esign.loadWarmingMapData();
    }
  });
};

esign.loadWarmingMapDataMeinshausen = function() {
  var pathX = 'assets/data/warming_consistency_x.json?v=' + esign.cache.version;
  var pathY = 'assets/data/warming_consistency_y.json?v=' + esign.cache.version;
  var pathDataMeinshausen = 'assets/data/warming_consistency_meinshausen.json?v=' + esign.cache.version;

  function closest(needle, haystack) {
    return haystack.reduce((a, b) => {
      let aDiff = Math.abs(a - needle);
      let bDiff = Math.abs(b - needle);

      if (aDiff === bDiff) {
        return a > b ? a : b;
      } else {
        return bDiff < aDiff ? b : a;
      }
    });
  }

  $.when(
    $.getJSON(pathDataMeinshausen, function(data) {
      esign.cache.warmingConsistencyDataMeinshausen = data;
    }),
    $.getJSON(pathX, function(data) {
      esign.cache.warmingConsistencyX = data;
    }),
    $.getJSON(pathY, function(data) {
      esign.cache.warmingConsistencyY = data;
    }),
    $.getJSON('assets/data/warming_low_meinshausen.json?v=' + esign.cache.version, function(dataMap) {
      esign.cache.warmingLowMeinshausen = dataMap;
    }),
    $.getJSON('assets/data/warming_high_meinshausen.json?v=' + esign.cache.version, function(dataMap) {
      esign.cache.warmingHighMeinshausen = dataMap;
    }),
  ).done(setMeinshausenValues);

  function setMeinshausenValues() {
    $.when(
      (function () {
        var keys = ['r', 'g', 'b', 'temp-avg-x', 'em-avg-y', 'temp-high-x', 'em-high-y', 'temp-low-x', 'em-low-y', 'em-high-y-min', 'em-high-y-max', 'em-low-y-min', 'em-low-y-max'];
        var xAxis = esign.cache.warmingConsistencyX[0].x;

        esign.cache.warmingConsistencyDataMeinshausen.forEach(function(item) {
          var consistency = esign.cache.warmingConsistencyY.filter(function(value) {
            return Object.keys(value)[0] === item['iso-a3'];
          })[0];
          var low = esign.cache.warmingLowMeinshausen.filter(function(value) {
            return value['iso-a3'] === item['iso-a3'];
          })[0];
          var high = esign.cache.warmingHighMeinshausen.filter(function(value) {
            return value['iso-a3'] === item['iso-a3'];
          })[0];

          if (consistency) {
            consistency = consistency[item['iso-a3']];
          }

          for (var i = 0; i < keys.length; i++) {
            if (typeof item[keys[i]] === 'string') {
              if (item[keys[i]]) {
                item[keys[i]] = item[keys[i]].replace(',','.');
                item[keys[i]] = parseFloat(item[keys[i]]);

                if (consistency) {
                  if (keys[i].indexOf('-y') > -1) {
                    item[keys[i]] = closest(item[keys[i]], consistency);
                  }
                }
              }
            }
          }

          if (consistency) {
            item['temp-avg-x'] = xAxis[consistency.indexOf(item['em-avg-y'])];
            item['temp-high-x'] = xAxis[consistency.indexOf(item['em-high-y'])];
            item['temp-low-x'] = xAxis[consistency.indexOf(item['em-low-y'])];

            if (item['temp-high-x']) {
              high.value = Math.round(item['temp-high-x'] * 10) / 10;
            }

            if (item['temp-low-x']) {
              low.value =  Math.round(item['temp-low-x'] * 10) / 10;
            }
          }
        });

        esign.cache.warmingLowMeinshausenEU27 = esign.cache.warmingLowMeinshausen;
        esign.cache.warmingLowMeinshausenEU28 = esign.cache.warmingLowMeinshausen;
        esign.cache.warmingHighMeinshausenEU27 = esign.cache.warmingHighMeinshausen;
        esign.cache.warmingHighMeinshausenEU28 = esign.cache.warmingHighMeinshausen;
      })(),
    ).done(function() {
      esign.loadWarmingMapData();
    });
  }
};

/* Load warming map data */
esign.loadWarmingMapData = function() {
  esign.cache.filenameMap = 'Global 2100-warming of NDCs [Downloaded from Paris-equity-check.org]';

  if (
    (esign.cache.warmingModeUpdated && esign.cache.warmingSource === 1)
    && (!esign.cache.warmingConsistencyDataMeinshausen || !esign.cache.warmingConsistencyX || !esign.cache.warmingConsistencyY)
  ) {
    esign.loadWarmingMapDataMeinshausen();
    return;
  }

  function loadWarmingType(type) {
    var dataName = 'warming' + type;
    var dataNameEU27 = 'warming' + type + 'EU27';
    var dataNameEU28 = 'warming' + type + 'EU28';
    var dataNameUpdated = 'warming' + type + 'Updated';
    var dataNameUpdatedEU27 = 'warming' + type + 'UpdatedEU27';
    var dataNameUpdatedEU28 = 'warming' + type + 'UpdatedEU28';

    var mode = 0;

    if (type === 'High') {
      mode = 1;
    }

    if (type === 'Low') {
      mode = 2;
    }

    if (esign.cache.warmingMode === mode) {
      if (esign.cache.warmingModeUpdated) {
        if (esign.cache.warmingSource === 0) {
          if (!esign.cache[dataNameUpdated]) {
            $.getJSON('assets/data/warming_' + type.toLowerCase() + '_updated.json?v=' + esign.cache.version, function(dataMap) {
              esign.cache[dataNameUpdated] = dataMap;

              var EU27 = esign.getValueByIso(esign.cache[dataNameUpdated], 'EU27');
              var EU28 = esign.getValueByIso(esign.cache[dataNameUpdated], 'EU28');

              for (var i = 0; i < esign.cache[dataNameUpdated].length; i += 1) {
                if (esign.cache.countriesEU27.indexOf(esign.cache[dataNameUpdated][i]['iso-a3']) > -1) {
                  esign.cache[dataNameUpdatedEU27].push({
                    'iso-a3': esign.cache[dataNameUpdated][i]['iso-a3'],
                    value: EU27.value
                  });
                } else {
                  esign.cache[dataNameUpdatedEU27].push(esign.cache[dataNameUpdated][i]);
                }

                if (esign.cache.countriesEU28.indexOf(esign.cache[dataNameUpdated][i]['iso-a3']) > -1) {
                  esign.cache[dataNameUpdatedEU28].push({
                    'iso-a3': esign.cache[dataNameUpdated][i]['iso-a3'],
                    value: EU28.value
                  });
                } else {
                  esign.cache[dataNameUpdatedEU28].push(esign.cache[dataNameUpdated][i]);
                }

                if (esign.cache.warmingNonAssessedUpdatedTargets.indexOf(esign.cache[dataNameUpdated][i]['iso-a3']) > -1) {
                  esign.cache[dataNameUpdated][i].color = '#E4F2F9';
                }
              }

              esign.drawWarmingMap(esign.cache[dataNameUpdatedEU27], 'EU27');
            });
          } else {
            esign.drawWarmingMap(esign.cache[dataNameUpdatedEU27], 'EU27');
          }
        } else if (esign.cache.warmingSource === 1) {


          esign.drawWarmingMap(esign.cache['warming' + type + 'Meinshausen'], 'Meinshausen');
        }
      } else {
        if (!esign.cache[dataName]) {
          $.getJSON('assets/data/warming_' + type.toLowerCase() + '.json?v=' + esign.cache.version, function(dataMap) {
            esign.cache[dataName] = dataMap;

            var EU27 = esign.getValueByIso(esign.cache[dataName], 'EU27');
            var EU28 = esign.getValueByIso(esign.cache[dataName], 'EU28');

            for (var i = 0; i < esign.cache[dataName].length; i += 1) {
              if (esign.cache.countriesEU27.indexOf(esign.cache[dataName][i]['iso-a3']) > -1) {
                esign.cache[dataNameEU27].push({
                  'iso-a3': esign.cache[dataName][i]['iso-a3'],
                  value: EU27.value
                });
              } else {
                esign.cache[dataNameEU27].push(esign.cache[dataName][i]);
              }

              if (esign.cache.countriesEU28.indexOf(esign.cache[dataName][i]['iso-a3']) > -1) {
                esign.cache[dataNameEU28].push({
                  'iso-a3': esign.cache[dataName][i]['iso-a3'],
                  value: EU28.value
                });
              } else {
                esign.cache[dataNameEU28].push(esign.cache[dataName][i]);
              }
            }

            esign.drawWarmingMap(esign.cache[dataNameEU28], 'EU28');
          });
        } else {
          esign.drawWarmingMap(esign.cache[dataNameEU28], 'EU28');
        }
      }
    }
  }

  loadWarmingType('Avg');
  loadWarmingType('High');
  loadWarmingType('Low');
};

/* Draw map, data of each single country must be in the 1.2-5.1 range to maintain correct color coding */
esign.drawWarmingMap = function(data, seriesName) {
  // Allow nullInteraction fix
  Highcharts.seriesTypes.map.prototype.pointClass.prototype.onMouseOver = function (e) {
    clearTimeout(this.colorInterval);
    if (this.value !== null || this.series.options.nullInteraction) {
      Highcharts.Point.prototype.onMouseOver.call(this, e);
    } else {
      this.series.onMouseOut(e);
    }
  }

  var seriesOptions = function (seriesData, seriesName) {
    return {
      name: seriesName,
      data: seriesData,
      mapData: Highcharts.geojson(Highcharts.maps['custom/world-eckert3-highres']),
      backgroundColor: '#FCFFC5',
      joinBy: 'iso-a3',
      cursor: 'pointer',
      nullInteraction: esign.cache.warmingModeUpdated,
      states: {
        hover: {
          borderColor: 'grey',
          color: this.color
        }
      },
      dataLabels: {
        enabled: false,
        useHTML: true
      },
      point: {
        events:{
          click: function (event) {
            esign.cache.dataSelected = true;
            esign.cache.isoSelected = this['iso-a3'];

            if (this.series && this.series.name && this.series.name === 'EU27' && esign.cache.countriesEU27.indexOf(esign.cache.isoSelected) > -1) {
              esign.cache.isoSelected = 'EU27';

            } else if (this.series && this.series.name && this.series.name === 'EU28' && esign.cache.countriesEU28.indexOf(esign.cache.isoSelected) > -1) {
              esign.cache.isoSelected = 'EU28';
            }

            for (var i = 0; i < esign.cache.countries.length; i++ ) {
              if(esign.cache.countries[i]['iso-a3'] === esign.cache.isoSelected) esign.cache.countrySelected = esign.cache.countries[i]['country'];
            }

            if (event.point) {
              if (event.point.options && !event.point.options.labelrank) {
                esign.openWarmingChart(this.color, temperatureDisplayValue(this.options.value));
              }
            } else {
              esign.openWarmingChart(this.color, temperatureDisplayValue(this.options.value));
            }
          },
          mouseOver: function (event) {
            if (event.target && event.target.options && !event.target.options.labelrank) {
              event.target.graphic.element.classList.remove('highcharts-null-point')
            }
          },
          mouseOut: function () {

          }
        }
      },
      nullColor: '#F1F1F1',
      borderColor: '#D2D2D2',
      borderWidth: 1
    };
  }

  esign.cache.map = new Highcharts.Map('map2', {
    title: {
      text: esign.cache.filenameMap,
      style: {
        fontSize: '8'
      },
      useHTML: true
    },
    exporting: {
      enabled: false,
      filename: esign.cache.filenameMap,
      fallbackToExportServer: true,
      chartOptions: {
        credits: {
          enabled: true,
          href:"#",
          position: {
            align:"right",
            verticalAlign:"bottom",
            x: -10,
            y: -5
          },
          style: {"cursor": "default", "font-size":"6pt"},
          text:"From Paris-equity-check.org, based on Robiou du Pont et al. (2018), Nature Communications"
        },
        chart:{
          events: {
            load: function(event) {
              this.renderer.image('assets/images/legend-warming.png',10,260,100,129).add();
            }
          }
        }
      }
    },
    chart: {
      margin: [0, 0, 0, 0],
      style: {
        fontFamily: 'Open Sans'
      },
      backgroundColor: '#f6faff',
      pinchType: 'none',
    },
    credits: false,
    mapNavigation: {
      enabled: true,
      enableMouseWheelZoom: false,
      enableDoubleClickZoom: false,
      enableDoubleClickZoomTo: false,
      buttonOptions: {
        align: 'right',
        verticalAlign: 'bottom',
        x: -20,
        theme: {
          fill: 'white',
          'stroke-width': 1,
          stroke: 'silver',
          r: 0,
          states: {
            hover: {
              fill: '#f5f5f5'
            }
          }
        }
      },
      buttons: {
        zoomIn: {
          y: -20
        },
        zoomOut: {
          y: 8
        }
      }
    },
    colorAxis: {
      stops: [
        [0.1, '#74AB00'],
        [0.2, '#E1E000'],
        [0.3, '#E19300'],
        [0.5, '#BF5700'],
        [0.6, '#BF0000'],
        [0.8, '#6E0000']
      ]
    },
    legend: {
      enabled: false,
      align: 'center',
      x: 0,
      y: 10,
      color: '#fff',
      layout: 'horizontal',
      floating: false,
      verticalAlign: 'bottom',
      shadow: false,
      border: 0,
      borderRadius: 0,
      borderWidth: 0
    },
    tooltip: {
      enabled: !esign.cache.isMobile,
      useHTML: true,
      borderWidth:0,
      borderRadius:0,
      padding: 5,
      minWidth: 300,
      backgroundColor: false,
      shadow: false,
      hideDelay: 30,
      formatter: function () {
        var iso = this.point['iso-a3'],
          name;

        for (var i = 0; i < esign.cache.countries.length; i++) {
          if(esign.cache.countries[i]['iso-a3'] === iso) {
            name = esign.cache.countries[i]['country'];
            break;
          }
        }

        if (name === undefined) {
          return false;
        }

        if (esign.cache.warmingModeUpdated && esign.cache.warmingNonAssessedUpdatedTargets.indexOf(iso) > -1) {
          return '<div class="tool"><h4>' + name
            + '</h4><p style="margin-bottom: 5px;"><a href="https://unfccc.int/NDCREG" target="_blank" rel="noopener">Target updated but not assessed here</a></p><div class="approach">' + temperatureDisplayValue(this.point.options.value) + '</div>'
            + '</div>';
        }

        if (iso === 'EU27' || iso === 'EU28') {
          return '<div class="tool"><h4>' + name
            + '</h4><div class="approach">' + temperatureDisplayValue(this.point.options.value) + '</div>'
            + '</div>';
        } else if (this.series && this.series.name && this.series.name === 'EU27' && esign.cache.countriesEU27.indexOf(iso) > -1) {
          var tempCountry = esign.cache.warmingMode === 2 ? esign.getValueByIso(esign.cache.warmingLowUpdated, iso).value : esign.getValueByIso(esign.cache.warmingHighUpdated, iso).value;
          return '<div class="tool"><h4>' + 'European Union (27)'
            + '</h4><div class="approach">' + '2.3 °C' + '</div><hr>'
            + '<h4>' + name
            + '</h4><p style="margin-bottom: 5px;">Select in country list to look at individual EU member state</p><div class="approach">' + temperatureDisplayValue(tempCountry) + '</div></div></div>';
        } else if (this.series && this.series.name && this.series.name === 'EU28' && esign.cache.countriesEU28.indexOf(iso) > -1) {
          var temp = null;

          if (esign.cache.warmingMode === 0) {
            temp = esign.getValueByIso(esign.cache.warmingAvg, iso).value;
          }

          if (esign.cache.warmingMode === 1) {
            temp = esign.getValueByIso(esign.cache.warmingHigh, iso).value;
          }

          if (esign.cache.warmingMode === 2) {
            temp = esign.getValueByIso(esign.cache.warmingLow, iso).value;
          }

          return '<div class="tool"><h4>' + 'European Union (28)'
            + '</h4><div class="approach">' + '3.2 °C' + '</div><hr>'
            + '<h4>' + name
            + '</h4><p style="margin-bottom: 5px;">Select in country list to look at individual EU member state</p><div class="approach">' + temperatureDisplayValue(temp) + '</div></div></div>';
        } else {
          return '<div class="tool"><h4>' + name
            + '</h4><div class="approach">' + temperatureDisplayValue(this.point.options.value) + '</div></div>';
        }
      }
    },
    series: [seriesOptions(data, seriesName)]
  });

  function temperatureDisplayValue(value) {
    var ndc;
    var offLimit = '';

    value != null ? ndc = value : ndc = 'N/A';

    if (ndc === 5.1 || ndc > 5.1) {
      offLimit = '&ge; ';
      ndc = 5.1;
    } else if (ndc === 1.2 || ndc < 1.2) {
      offLimit = '&le; ';
      ndc = 1.2;
    }

    if (ndc === 'N/A') {
      return ndc;
    }

    return offLimit + ndc + '°C';
  }

  // Listeners
  var countryListItem = $('#country-list ul li'),
    tooltip, iso;

  countryListItem.click(function (event) {
    esign.cache.dataSelected = true;
    iso = this.getAttribute('data-iso');
    if(esign.cache.isoSelected !== iso) {
      for(var i = 0; i < esign.cache.map.series[0].data.length; i++) {
        if(esign.cache.map.series[0].data[i]['iso-a3'] === iso) {
          tooltip = esign.cache.map.series[0].data[i];
          tooltip.firePointEvent('click', event);
        }
      }
    }
  });

  countryListItem.mouseover(function () {
    if(!esign.cache.isMobile) {
      iso = this.getAttribute('data-iso');
      for(var i = 0; i < esign.cache.map.series[0].data.length; i++) {
        if(esign.cache.map.series[0].data[i]['iso-a3'] === iso) {
          tooltip = esign.cache.map.series[0].data[i];

          if (tooltip && tooltip.setState) {
            tooltip.setState('hover');
          }

          esign.cache.map.tooltip.update({
            positioner: function(val, val2, val3) {
              if (iso === 'EU27' || iso === 'EU28') {
                return { x: this.chart.plotWidth/2, y: this.chart.plotHeight/3 };
              }
              return { x: val3.plotX, y: val3.plotY };
            }
          });

          esign.cache.map.tooltip.refresh(tooltip);
        }
      }
    }
  });

  countryListItem.mouseout(function () {
    if(!esign.cache.isMobile) {
      if (esign.cache.map !== undefined) {
        if (tooltip && tooltip.setState) {
          tooltip.setState();
        }
        esign.cache.map.tooltip.hide();
      }
    }
  });

  countryListItem.mouseenter(function () {
    if(!esign.cache.isMobile) {
      var iso = this.getAttribute('data-iso');

      if (esign.cache.warmingModeUpdated) {
        if (iso === 'EU27') {
          esign.handleWarmingDataSeries(seriesOptions, 'EU27');
        } else if (esign.cache.countriesEU27.indexOf(iso) > -1) {
          esign.handleWarmingDataSeries(seriesOptions);
        }
      } else {
        if (iso === 'EU28') {
          esign.handleWarmingDataSeries(seriesOptions, 'EU28');
        } else if (esign.cache.countriesEU28.indexOf(iso) > -1) {
          esign.handleWarmingDataSeries(seriesOptions);
        }
      }
    }
  });

  countryListItem.mouseleave(function () {
    if(!esign.cache.isMobile) {
      var iso = this.getAttribute('data-iso');

      if (esign.cache.warmingModeUpdated) {
        if (esign.cache.countriesEU27.indexOf(iso) > -1) {
          esign.handleWarmingDataSeries(seriesOptions, 'EU27');
        }
      } else {
        if (esign.cache.countriesEU28.indexOf(iso) > -1) {
          esign.handleWarmingDataSeries(seriesOptions, 'EU28');
        }
      }
    }
  });
};

esign.handleWarmingDataSeries = function(seriesOptions, type = '') {
  esign.cache.map.series[0].remove(true);

  if (esign.cache.warmingMode === 0) {
    if (esign.cache.warmingModeUpdated) {
      esign.cache.map.addSeries(seriesOptions(esign.cache['warmingAvgUpdated' + type], '' + type));
    } else {
      esign.cache.map.addSeries(seriesOptions(esign.cache['warmingAvg' + type], '' + type));
    }
  }

  if (esign.cache.warmingMode === 1) {
    if (esign.cache.warmingModeUpdated) {
      if (esign.cache.warmingSource === 0) {
        esign.cache.map.addSeries(seriesOptions(esign.cache['warmingHighUpdated' + type], '' + type));
      } else {
        esign.cache.map.addSeries(seriesOptions(esign.cache['warmingHighMeinshausen' + type], '' + type));
      }
    } else {
      esign.cache.map.addSeries(seriesOptions(esign.cache['warmingHigh' + type], '' + type));
    }
  }

  if (esign.cache.warmingMode === 2) {
    if (esign.cache.warmingModeUpdated) {
      if (esign.cache.warmingSource === 0) {
        esign.cache.map.addSeries(seriesOptions(esign.cache['warmingLowUpdated' + type], '' + type));
      } else {
        esign.cache.map.addSeries(seriesOptions(esign.cache['warmingLowMeinshausen' + type], '' + type));
      }
    } else {
      esign.cache.map.addSeries(seriesOptions(esign.cache['warmingLow' + type], '' + type));
    }
  }
}

/* Open warming chart view */
esign.openWarmingChart = function(color, temperature) {
  var $graphCountryTitle = $('.graph-country__title');
  var $graphCountryTemperature = $('.graph-country__temperature');
  $('.modal-button-graph').trigger('click');
  $graphCountryTitle.text(esign.cache.countrySelected);
  $graphCountryTemperature.html(temperature);

  esign.fairnessStatement(esign.cache.isoSelected);

  if (
    (esign.cache.warmingModeUpdated && !esign.cache.warmingConsistencyDataUpdated)
    || (!esign.cache.warmingModeUpdated && !esign.cache.warmingConsistencyData)
  ) {
    esign.loadWarmingChartData(color);
  } else {
    esign.initWarmingChartEvents(color);
  }
};

/* Load warming chart data */
esign.loadWarmingChartData = function (color) {
  var pathX = 'assets/data/warming_consistency_x.json?v=' + esign.cache.version;
  var pathY = 'assets/data/warming_consistency_y.json?v=' + esign.cache.version;
  var pathTrajectory1 = 'assets/data/trajectories_1p5c.json?v=' + esign.cache.version;
  var pathTrajectory2 = 'assets/data/trajectories_2c.json?v=' + esign.cache.version;
  var pathData = 'assets/data/warming_consistency.json?v=' + esign.cache.version;
  var pathDataUpdated = 'assets/data/warming_consistency_updated.json?v=' + esign.cache.version;

  $.when(
    $.getJSON(pathData, function(data) {
      esign.cache.warmingConsistencyData = data;
    }),
    $.getJSON(pathDataUpdated, function(data) {
      esign.cache.warmingConsistencyDataUpdated = data;
    }),
    $.getJSON(pathX, function(data) {
      esign.cache.warmingConsistencyX = data;
    }),
    $.getJSON(pathY, function(data) {
      esign.cache.warmingConsistencyY = data;
    }),
    $.getJSON(pathTrajectory1, function(data) {
      esign.cache.warmingTrajectory1 = data;
    }),
    $.getJSON(pathTrajectory2, function(data) {
      esign.cache.warmingTrajectory2 = data;
    })
  ).done(function() {
    esign.initWarmingChartEvents(color);
  });
};

/* Init warming chart events */
esign.initWarmingChartEvents = function (color) {
  // Relative to
  $('.controls--w1 .rel-year').click(function (e) {
    e.preventDefault();

    // esign.cache.isAbsolute = false;
    esign.cache.$relativeTrigger.addClass('active');
    // esign.cache.$absoluteTrigger.removeClass('active');

    var year = this.text.replace('rel-', '');
    esign.cache.$relativeTrigger.html('Relative to ' + year + ' <span class="caret"></span><span class="sr-only">Toggle Dropdown</span>');

    esign.createWarmingChart(year, color);
  });

  $('.controls--w2 .rel-year').click(function (e) {
    e.preventDefault();

    esign.cache.isAbsolute = false;
    esign.cache.$relativeTrigger2.addClass('active');
    esign.cache.$absoluteTrigger2.removeClass('active');

    var year = this.text.replace('rel-', '');
    esign.cache.$relativeTrigger2.html('Relative to ' + year + ' <span class="caret"></span><span class="sr-only">Toggle Dropdown</span>');

    esign.createTrajectoryChart(year);
  });

  esign.cache.$absoluteTrigger2.click(function (e) {
    e.preventDefault();

    if (!esign.cache.isAbsolute) {
      esign.cache.isAbsolute = true;
      esign.cache.$relativeTrigger2.removeClass('active');
      esign.cache.$absoluteTrigger2.addClass('active');

      esign.createTrajectoryChart(2010);
    }
  });

  esign.createWarmingChart(2010, color);
  esign.createTrajectoryChart(2010);
}

/* Create warming chart */
esign.createWarmingChart = function(year, color = esign.cache.color.black) {
  var warmingConsistencyData = esign.cache.warmingConsistencyData;

  if (esign.cache.warmingModeUpdated) {
    if (esign.cache.warmingSource === 0) {
      warmingConsistencyData = esign.cache.warmingConsistencyDataUpdated
    } else {
      warmingConsistencyData = esign.cache.warmingConsistencyDataMeinshausen;
    }
  }

  var countryData = warmingConsistencyData.filter(getCountryIso)[0];

  function getCountryIso(value) {
    return value['iso-a3'] === esign.cache.isoSelected;
  }

  function getCountryIsoTrajectory(value) {
    return value['iso-a3'] === "'" + esign.cache.isoSelected + "'";
  }

  var dataTrajectory = esign.cache.warmingTrajectory1.filter(getCountryIsoTrajectory);

  var colorDark = tinycolor(color).darken(10).toString();
  var colorLight = tinycolor(color).lighten(30).toString();
  var xAxis = esign.cache.warmingConsistencyX[0].x;
  var yAxis;
  var chartData = [];

  var levels2010 = dataTrajectory[0][2010];
  var levelsRel = dataTrajectory[0][year];

  var chart;

  for (var i = 0; i < esign.cache.warmingConsistencyY.length; i++) {
    if (Object.keys(esign.cache.warmingConsistencyY[i])[0] === esign.cache.isoSelected) {
      yAxis = esign.cache.warmingConsistencyY[i][esign.cache.isoSelected];
      break;
    }
  }

  var minX = 1.179476;
  var maxX = 5.119476;
  var levelsCoefficient = levels2010 / levelsRel;

  var chartDotData = [
    [countryData['temp-avg-x'], (countryData['em-avg-y'] + 100) * levelsCoefficient]
  ];

  if (countryData['temp-avg-x'] < minX) {
    chartDotData = [
      [minX, (countryData['em-avg-y'] + 100) * levelsCoefficient]
    ];
  }

  if (countryData['temp-avg-x'] > maxX) {
    chartDotData = [
      [maxX, (countryData['em-avg-y'] + 100) * levelsCoefficient]
    ];
  }

  if (!countryData['em-avg-y']) {
    chartDotData = null;
  }

  if (!countryData['temp-avg-x'] && countryData['em-avg-y']) {
    chartDotData = [
      [xAxis[yAxis.indexOf(countryData['em-avg-y'])], (countryData['em-avg-y'] + 100) * levelsCoefficient]
    ];
  }

  var chartDotHighData = [
    [countryData['temp-high-x'], (countryData['em-high-y'] + 100) * levelsCoefficient]
  ];

  if (countryData['temp-high-x'] < minX) {
    chartDotHighData = [
      [minX, (countryData['em-high-y'] + 100) * levelsCoefficient]
    ];
  }

  if (countryData['temp-high-x'] > maxX) {
    chartDotHighData = [
      [maxX, (countryData['em-high-y'] + 100) * levelsCoefficient]
    ];
  }

  if (!countryData['em-high-y']) {
    chartDotHighData = null;
  }

  if (!countryData['temp-high-x'] && countryData['em-high-y']) {
    chartDotHighData = [
      [xAxis[yAxis.indexOf(countryData['em-high-y'])], (countryData['em-high-y'] + 100) * levelsCoefficient]
    ];
  }

  var chartDotLowData = [
    [countryData['temp-low-x'], (countryData['em-low-y'] + 100) * levelsCoefficient]
  ];

  if (countryData['temp-low-x'] < minX) {
    chartDotLowData = [
      [minX, (countryData['em-low-y'] + 100) * levelsCoefficient]
    ];
  }

  if (countryData['temp-low-x'] > maxX) {
    chartDotLowData = [
      [maxX, (countryData['em-low-y'] + 100) * levelsCoefficient]
    ];
  }

  if (!countryData['em-low-y']) {
    chartDotLowData = null;
  }

  if (!countryData['temp-low-x'] && countryData['em-low-y']) {
    chartDotLowData = [
      [xAxis[yAxis.indexOf(countryData['em-low-y'])], (countryData['em-low-y'] + 100) * levelsCoefficient]
    ];
  }

  // Error bar series
  var chartErrorDataLowData = [
    [
      (countryData['em-low-y-min'] ? (countryData['em-low-y-min'] + 100) * levelsCoefficient : null),
      (countryData['em-low-y-max'] ? (countryData['em-low-y-max']+ 100) * levelsCoefficient : null)
    ]
  ];

  if (!countryData['em-low-y-min'] || !countryData['em-low-y-max']) {
    chartErrorDataLowData = null;
  }

  var chartErrorDataHighData = [
    [
      (countryData['em-high-y-min'] ? (countryData['em-high-y-min'] + 100) * levelsCoefficient : null),
      (countryData['em-high-y-max'] ? (countryData['em-high-y-max']+ 100) * levelsCoefficient : null)
    ]
  ];

  if (!countryData['em-high-y-min'] || !countryData['em-high-y-max']) {
    chartErrorDataHighData = null;
  }

  // console.log('avg', xAxis[yAxis.indexOf(countryData['em-avg-y'])]);
  // console.log('high', xAxis[yAxis.indexOf(countryData['em-high-y'])]);
  // console.log('low', xAxis[yAxis.indexOf(countryData['em-low-y'])]);

  for (var j = 0; j < xAxis.length; j++) {
    var newYValue = (yAxis[j] + 100) * levelsCoefficient;
    chartData.push([xAxis[j], newYValue]);
  }

  var chartOptions = {
    chart: {
      renderTo: 'chart',
      style: {
        fontFamily: 'Open Sans'
      },
      backgroundColor: '#ffffff'
    },
    title: {
      text: esign.cache.countrySelected,
      useHTML: true
    },
    subtitle: {
      text: '',
      style: {
        color: '#B4B4B4',
        fontSize: '5'
      },
      useHTML: true
    },
    xAxis: {
      title: {
        text: '2100-warming in °C (>50% likelihood)',
        style: {
          fontSize: '14px'
        }
      },
      plotLines: [
        {
          color: '#74AB00',
          value: 1.5,
          width: 1,
          label: {
            text: '1.5°C',
            rotation: 270,
            align: 'right',
            x: -8
          }
        },
        {
          color: '#E1E000',
          value: 1.7,
          width: 1,
          label: {
            text: 'well below 2°C',
            rotation: 270,
            align: 'right',
            x: -8
          }
        }
      ]
    },
    yAxis: {
      title: {
        text: 'Emissions allowances in % of ' + year + ' levels',
        style: {
          fontSize: '14px'
        }
      },
      plotLines: [{
        value: 0,
        color: 'black',
        width: 2
      }],
      crosshair: true
    },
    tooltip: {
      crosshairs: true,
      shared: true,
      useHtml: true,
      borderWidth: 0,
      hideDelay: 30,
      style: {
        fontSize: '15px'
      }
    },
    legend: {
      title: {
        text: 'Legend',
        style: {
          fontWeight: 'normal'
        }
      },
      enabled: true,
      layout: 'vertical',
      floating: false,
      align: 'center left',
      itemStyle: {
        fontWeight: 'normal',
        fontSize: '10px'
      },
      itemMarginBottom: 1,
    },
    exporting: {
      enabled: false,
      filename: esign.cache.countrySelected + ' - 2100-warming as a function of national 2030-emissions',
      buttons: {
        contextButton: {
          verticalAlign: 'bottom',
          text: 'export'
        }
      },
      fallbackToExportServer: true,
      chartOptions: {
        credits: {
          enabled: true,
          href: "#",
          position: {
            align: "right",
            verticalAlign: "bottom",
            x: -10,
            y: -190,
          },
          style: {"cursor": "default", "font-size": "6pt"},
          text: "From Paris-equity-check.org, based on Robiou du Pont et al. (2018), Nature Communications"
        }
      }
    },
    credits: false,
    plotOptions: {
      line: {
        events: {
          legendItemClick: function () {
            return false;
          }
        }
      }
    },
    series: [{
      name: 'National emissions change over 2010-2030 as a function of 2100 global warming',
      data: chartData,
      zIndex: 10,
      color: esign.cache.color.black,
      marker: {
        lineWidth: 0,
        radius: 0,
        symbol: 'circle',
        lineColor: 'transparent'
      },
      zoneAxis: 'x'
    }]
  };

  chart = new Highcharts.Chart(chartOptions);

  chart.tooltip.options.formatter = function() {
    var points = this.points || Highcharts.splat(this),
      txt = 'Implied global warming: ',
      temp,
      roundedValue = esign.round(this.x, 1);

    if (
      this.series &&
      (this.series.name === 'NDC average assessment' ||
        this.series.name === 'NDC high assessment (with unconditional pledges)' ||
        this.series.name === 'NDC low assessment (with conditional pledges)')
    ) {
      if (roundedValue >= 5.1) {
        temp = '&ge; 5.1';
      } else if (roundedValue <= 1.2) {
        temp = '&le; 1.2';
      } else {
        temp = roundedValue;
      }
    } else {
      temp = roundedValue;
    }

    txt += '<span class=""><strong>' + esign.decodeHTML(temp) + ' °C</strong></span><br>';

    Highcharts.each(points, function(p, i) {
      if (!p.point.whiskers) {
        txt += 'Emissions level: <strong>' + esign.round(p.y, 0) + '</strong>';
      }
    });

    return txt;
  };

  var lowSeriesName = 'NDC low assessment (with conditional pledges)';
  var highSeriesName = 'NDC high assessment (with unconditional pledges)';

  if (esign.cache.warmingModeUpdated) {
    lowSeriesName = 'NDC unconditional assessment';
    highSeriesName = 'NDC conditional assessment';

    if (esign.cache.isoSelected === 'KOR') {
      lowSeriesName = 'Proposed target (June 2021)';
      highSeriesName = '2020 NDC update';
    }
  }

  chart.addSeries({
    id: 'ndc-low',
    name: lowSeriesName,
    data: chartDotLowData,
    type: 'scatter',
    zIndex: 999,
    marker: {
      lineWidth: 1,
      radius: 8,
      lineColor: esign.cache.warmingModeUpdated ? color : colorLight,
      fillColor: esign.cache.warmingModeUpdated ? color : '#f6faff',
      symbol: 'circle',
      enabled: !!chartDotLowData
    },
    showInLegend: true
  });

  if (chartErrorDataLowData) {
    chart.addSeries({
      name: 'NDC low assessment uncertainty range',
      data: chartErrorDataLowData,
      type: 'errorbar',
      zIndex: 998,
      showInLegend: false,
      linkedTo: 'ndc-low',
      pointStart: chartDotLowData[0][0],
      stemWidth: 2,
      pointWidth: 3
    });
  }

  chart.addSeries({
    id: 'ndc-high',
    name: highSeriesName,
    data: chartDotHighData,
    type: 'scatter',
    zIndex: 999,
    marker: {
      lineWidth: 1,
      radius: 8,
      lineColor: colorDark,
      fillColor: '#f6faff',
      symbol: 'circle',
      enabled: !!chartDotHighData
    },
    showInLegend: true
  });

  if (chartErrorDataHighData) {
    chart.addSeries({
      name: 'NDC high assessment uncertainty range',
      data: chartErrorDataHighData,
      type: 'errorbar',
      zIndex: 998,
      showInLegend: false,
      linkedTo: 'ndc-high',
      pointStart: chartDotHighData[0][0],
      stemWidth: 2,
      pointWidth: 3
    });
  }

  if (!esign.cache.warmingModeUpdated) {
    chart.addSeries({
      name: 'NDC average assessment',
      data: chartDotData,
      type: 'scatter',
      zIndex: 999,
      marker: {
        lineWidth: 2,
        radius: 8,
        lineColor: color,
        fillColor: color,
        symbol: 'circle',
        enabled: !!chartDotData
      },
      showInLegend: true
    });
  }

  //add new pledge
  chart.addSeries({
    visible:false,
    name: 'Custom pledge',
    data: chartDotData,
    type: 'scatter',
    zIndex: 999,
    marker: {
      symbol:
        "url(data:image/png;base64,)"
      ,
      lineWidth: 2,
      radius: 8,
      lineColor: 'black',
      fillColor: 'black'
    },
    className: 'indc-tour',
    showInLegend: false
  });

  var updateCustomPledge = function (emission) {
    $(chart.series).each(function(i, serie) {
      if(serie.name === 'Custom pledge') {
        var closestY = chart.series[0].processedYData.reduce(function(prev, curr) {
          return (Math.abs(curr - emission) < Math.abs(prev - emission) ? curr : prev);
        });
        var closestYIndex = chart.series[0].processedYData.indexOf(closestY);
        var x = chart.series[0].processedXData[closestYIndex];
        serie.setData([[x,emission]]);
        serie.show();
      }
    });
  };

  var $customMarker = $('.customMarker');
  var $customMarkerClose = $('#custom-pledge-close');

  // showPledgeForm
  $('#addPlegde').click(function () {
    $customMarker.fadeIn();
    $customMarkerClose.fadeIn();
  });

  $customMarkerClose.click(function () {
    $customMarker.fadeOut();
    $customMarkerClose.fadeOut();
  });

  $('#updatePledge').click(function () {
    var emission = $('#emissions').val();

    if($.isNumeric(emission)){
      $customMarker.fadeOut();
      $customMarkerClose.fadeOut();

      updateCustomPledge(parseFloat(emission));
    }
  });

  $('#emissions').keypress(function (event) {
    if (event.keyCode === 13) {
      $('#updatePledge').click();
    }
  });

  // Exports
  esign.resetExportListeners();

  $('#export-chart-print').click(function () {
    chart.print();
  });
  $('#export-chart-png').click(function () {
    chart.exportChartLocal({
      type: 'image/png'
    });
  });
  $('#export-chart-jpg').click(function () {
    chart.exportChartLocal({
      type: 'image/jpeg'
    });
  });
  $('#export-chart-pdf').click(function () {
    chart.exportChartLocal({
      type: 'application/pdf'
    });
  });
  $('#export-chart-svg').click(function () {
    chart.exportChartLocal({
      type: 'image/svg+xml'
    });
  });

  // Show help
  $('#openRead').click(function () {
    $('#how-to-read').fadeIn();
    $('#how-to-read-close').fadeIn();
  });

  $('#how-to-read-close').click(function () {
    $('#how-to-read').fadeOut();
    $('#how-to-read-close').fadeOut();
  });

  $('#openRead2').click(function () {
    $('#how-to-read2').fadeIn();
    $('#how-to-read-close2').fadeIn();
  });

  $('#how-to-read-close2').click(function () {
    $('#how-to-read2').fadeOut();
    $('#how-to-read-close2').fadeOut();
  });

};

esign.createTrajectoryChart = function (year) {
  var yearMin = 1990,
    yearMax = 2100,
    unitK = 'ktCO2eq',
    unitM = 'MtCO2eq',
    unitG = 'GtCO2eq',
    unit = unitK,
    divider = 1;

  function getCountryIso(value) {
    return value['iso-a3'] === "'" + esign.cache.isoSelected + "'";
  }

  var dataTrajectories1 = esign.cache.warmingTrajectory1.filter(getCountryIso);
  var dataTrajectories2 = esign.cache.warmingTrajectory2.filter(getCountryIso);
  var trajectory1 = [], trajectory2 = [], trajectoryHistory = [];
  var maxValue = 0;

  if(esign.cache.isAbsolute) {
    for(var i = yearMin; i <= yearMax; i++) {
      trajectory1.push([i, dataTrajectories1[0][i]]);
      trajectory2.push([i, dataTrajectories2[0][i]]);
      max([dataTrajectories1[0][i], dataTrajectories2[0][i]]);
    }
  } else {
    for(var i = yearMin; i <= yearMax; i++) {
      // if(i >= year) {
        trajectory1.push([i, (dataTrajectories1[0][i]/dataTrajectories1[0][year])*100]);
        trajectory2.push([i,(dataTrajectories2[0][i]/dataTrajectories2[0][year])*100]);
      // }
    }
  }


  for(var i = 0; i <= 20; i++) {
    trajectoryHistory.push(trajectory1[i]);
  }

  function max(arr) {
    var m = Math.max.apply(null, arr);
    if(m > maxValue) maxValue = m;
  }

  if(maxValue > 999) {
    unit = unitM;
    divider = 1000;
  }
  if(maxValue > 999999) {
    unit = unitG;
    divider = 1000000;
  }

  var yAxis = {};

  if(esign.cache.isAbsolute) {
    // Absolute options
    yAxis = {
      title: {
        text: 'Emissions allowances in ' + unit
      },
      plotLines: [{
        value: 0,
        color: 'black',
        width: 2
      }],
      labels: {
        formatter: function () {
          if(unit === unitM) {
            return this.value / divider;
          } else if (unit === unitG) {
            return this.value / divider;
          }
          return this.value;
        }
      }
    };
    // filename = esign.cache.countrySelected + ' - Emissions allowances in ' + unit;
  } else {
    // Relative options
    yAxis = {
      title: {
        text: 'Emissions allowances in % of ' + year + ' levels'
      },
      plotLines: [{
        value: 100,
        color: 'black',
        dashStyle: 'shortdash',
        width: 2
      }, {
        value: 0,
        color: 'black',
        width: 2
      }],
      labels: {
        formatter: function () {
          return this.value;
        }
      }
    };
    // filename = esign.cache.countrySelected + ' - Emissions allowances in % of ' + relativeYear + ' levels (' + temp + ') [Downloaded from Paris-equity-check.org]';
  }


  var chartOptions = {
    chart: {
      renderTo: 'chart2',
      style: {
        fontFamily: 'Open Sans'
      },
      backgroundColor: '#ffffff'
    },
    title: {
     text: esign.cache.countrySelected,
      useHTML: true
    },
    subtitle: {
      text: '',
      style: {
        color: '#B4B4B4',
        fontSize: '5'
      },
      useHTML: true
    },
    xAxis: {
      title: {
        text: 'Year'
      }
    },
    yAxis: yAxis,
    tooltip: {
      crosshairs: true,
      shared: true,
      useHtml: true,
      borderWidth: 0,
      hideDelay: 30
    },
    legend: {
      title: {
        text: 'Legend',
        style: {
          fontWeight: 'normal'
        }
      },
      enabled: true,
      layout: 'vertical',
      floating: false,
      align: 'center left',
      itemStyle: {
        fontWeight: 'normal',
        fontSize: '10px'
      },
      itemMarginBottom: 1,
    },
    exporting: {
      enabled: false,
      filename: esign.cache.countrySelected + " - Emissions trajectories to stay below 2°C and 1.5°C",
      buttons: {
        contextButton: {
          verticalAlign: 'bottom',
          text: 'export'
        }
      },
      fallbackToExportServer: true,
      chartOptions:{
        credits: {
          enabled: true,
          href:"#",
          position: {
            align:"right",
            verticalAlign:"bottom",
            x: -10,
            y: -160,
          },
          style: {"cursor": "default", "font-size":"6pt"},
          text:"From Paris-equity-check.org, based on Robiou du Pont et al. (2018), Nature Communications"
        }
      }
    },
    credits: false,
    series: [{
      name: '2°C-scenario',
      data: trajectory2,
      zIndex: 10,
      color: '#E1E000',
      marker: {
        lineWidth: 0,
        radius: 0,
        symbol: 'circle',
        lineColor: 'transparent'
      },
      zoneAxis: 'x',
      zones: [{
        value: 2010,
        color: 'transparent'
      }]
    },{
      name: '1.5°C-scenario',
      data: trajectory1,
      zIndex: 10,
      color: '#74AB00',
      marker: {
        lineWidth: 0,
        radius: 0,
        symbol: 'circle',
        lineColor: 'transparent'
      },
      zoneAxis: 'x',
      zones: [{
        value: 2010,
        color: 'transparent'
      }]
    },{
      name: 'Historical emissions',
      data: trajectoryHistory,
      zIndex: 1,
      color: '#000000',
      marker: {
        lineWidth: 0,
        radius: 0,
        symbol: 'circle',
        lineColor: 'transparent'
      },
      zoneAxis: 'x',
      zones: [{
        value: 2010,
        color: '#000000'
      }]
    }]
  };

  var chart = new Highcharts.Chart(chartOptions);

  chart.tooltip.options.formatter = function() {
    var points = this.points || Highcharts.splat(this),
      txt = '';

    txt += '<span class="tooltip-year">' + this.x + '</span><br>';

    if(this.x <= 2010) {
      if(typeof this.point === 'undefined') {
        txt += 'Historical emissions: ' + (this.y/divider).toPrecision(3);
      }
    } else {
      if(esign.cache.isAbsolute) {
        Highcharts.each(points, function(p, i){
          var name = p.series.name;

          txt += name + ': ' + (p.y/divider).toPrecision(3) + ' ' + unit + '<br>';
        });
      } else {
        Highcharts.each(points, function(p, i){
          var name = p.series.name;

          txt += name + ': ' + (p.y/divider).toPrecision(3) + '%<br>';
        });
      }
    }
    return txt;
  };

  // Exports
  esign.resetExportListeners2();

  $('#export-chart-print2').click(function () {
    chart.print();
  });
  $('#export-chart-png2').click(function () {
    chart.exportChartLocal({
      type: 'image/png'
    });
  });
  $('#export-chart-jpg2').click(function () {
    chart.exportChartLocal({
      type: 'image/jpeg'
    });
  });
  $('#export-chart-pdf2').click(function () {
    chart.exportChartLocal({
      type: 'application/pdf'
    });
  });
  $('#export-chart-svg2').click(function () {
    chart.exportChartLocal({
      type: 'image/svg+xml'
    });
  });
};

esign.round = function (value, decimals) {
  return Number(Math.round(value+'e'+decimals)+'e-'+decimals);
};

esign.decodeHTML = function (html) {
  var txt = document.createElement('textarea');
  txt.innerHTML = html;
  return txt.value;
};

esign.getValueByIso = function(arr, iso) {
  function getCountryIso(value) {
    return value['iso-a3'] === iso;
  }
  return arr.filter(getCountryIso)[0];
}

